import React, {Component, useState} from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import styled from '@emotion/styled';

import {Location} from '@reach/router';
import {parse} from 'query-string';

import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import {Formik} from 'formik';
import * as Yup from 'yup';

import DefaultLayout from 'components/layouts/DefaultLayout';
import Title from 'components/Title';
import createUrl from 'utils/createUrl';
import navigation from '../../data/navigation/primary.yml';

import {NewsletterRequests} from 'agent';

type Props = {};

const TitleRow = styled(Row)`
  text-align: center;
  margin: auto;
`;

const ContentRow = styled(Row)`
  text-align: center;
  max-width: 60rem;
  margin: auto;
`;

const Text = styled.p`
  font-size: x-large;
`;

const today = new Date();

const schema = Yup.object({
  email: Yup.string()
    .required('Die E-Mail-Adresse muss angegeben werden!')
    .email('Die E-Mail-Adresse ist ungültig!'),
});

function submitForm(data) {
  const response = NewsletterRequests.create(data).then(
    function onResult(res) {
      alert('Newsletter-Anmeldung war erfolgreich!');
      window.widget.setState({success: true});
    },
    function onError(err) {
      var error =
        'Die Newsletter-Anmeldung war nicht erfolgriech! Die E-Mail-Adresse ist möglicherweise ungültig. Bei Problemen wende dich bitte an den Support.';
      alert(error);
      window.widget.setState({error: error});
    },
  );
}
var notSend = true;

const setNotSend = () => {
  notSend = false;
  return true;
};

function NewsletterRequestForm() {
  return (
    <Location>
      {({navigate, location}) => (
        <Formik
          validationSchema={schema}
          onSubmit={submitForm}
          initialValues={{
            email: parse(location.search).email || '',
          }}>
          {({
            handleSubmit,
            handleChange,
            handleBlur,
            values,
            touched,
            isValid,
            errors,
            submitForm,
          }) => (
            <Form noValidate onSubmit={handleSubmit}>
              <Form.Row>
                <Form.Group as={Col} md="12" controlId="validationEmail">
                  <Form.Label>E-Mail-Adresse</Form.Label>
                  <Form.Control
                    type="text"
                    name="email"
                    value={values.email}
                    onChange={handleChange}
                    isInvalid={!!errors.email}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.email}
                  </Form.Control.Feedback>
                </Form.Group>
              </Form.Row>
              <Button type="submit">Newsletter abonnieren</Button>
              {notSend &&
                parse(location.search).email &&
                setNotSend() &&
                submitForm()}
            </Form>
          )}
        </Formik>
      )}
    </Location>
  );
}

class NewsletterRequestWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {success: false, error: false};
  }

  componentDidMount() {
    window.widget = this;
  }

  render() {
    if (!this.state.success) {
      return (
        <div>
          <Title
            title="Newsletter abonnieren - Die Föderalen"
            url={createUrl('/newsletter')}
          />
          <TitleRow>
            <Col>
              <h1>Newsletter abonnieren</h1>
            </Col>
          </TitleRow>
          <ContentRow>
            <Col>
              <Text>
                Um das Newsletter zu bestellen, gib hier einfach deine
                E-Mail-Adresse ein. Wir schicken dir dann einen
                Bestätigungslink.
              </Text>
              {this.state.error && (
                <ContentRow>
                  <Col>
                    <Alert variant="danger">{this.state.error}</Alert>
                  </Col>
                </ContentRow>
              )}
              <NewsletterRequestForm />
            </Col>
          </ContentRow>
        </div>
      );
    } else {
      return (
        <div>
          <Title
            title="Zur Bestätigung der Newsletter-Anmeldung wurde eine E-Mail verschickt - Die Föderalen"
            url={createUrl('/newsletter')}
          />
          <TitleRow>
            <Col>
              <h1>
                Zur Bestätigung der Newsletter-Anmeldung wurde eine E-Mail
                verschickt!
              </h1>
            </Col>
          </TitleRow>
          <ContentRow>
            <Col>
              <Text>
                Vielen Dank! Um das kostenlose Newsletter zu bestätigen, erhälst
                du eine E-Mail mit einem Bestätigungslink.
              </Text>
            </Col>
          </ContentRow>
        </div>
      );
    }
  }
}

class Newsletter extends Component<Props> {
  render() {
    return (
      <DefaultLayout navigation={navigation}>
        <NewsletterRequestWidget />
      </DefaultLayout>
    );
  }
}

export default Newsletter;
